
@import "../../scss/_global.scss";

@mixin paddingContent() {
  @media (min-width: 993px) {
    padding: 0 40px 50px;

    @media (max-width: 1280px) {
      padding: 0 30px 40px;
    }

    @media (max-width: 1080px) {
      padding: 0 20px 30px;
    }
  }
}

#area {
  @extend %indent-p;

  .area-wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);

    @include gapWrapper();

    @media (max-width: 992px) {
      grid-template-rows: repeat(4, 1fr);
      grid-template-columns: 1fr;
    }
  }

  article {
    @extend %shadow-block;
    overflow: hidden;
  }

  .aria-item {
    display: flex;
    flex-direction: column;
    gap: 30px;

    @media (max-width: 1280px) {
      gap: 25px;
    }

    @media (max-width: 1024px) {
      gap: 20px;
    }

    @media (max-width: 992px) {
      flex-direction: row;
      align-items: center;
      gap: 50px;
    }

    @media (max-width: 425px) {
      gap: 20px;
    }

    &__img {
      width: 100%;

      img {
        width: 100%;
      }

      @media (max-width: 992px) {
        width: auto;
        img {
          width: auto;
          height: 100%;
          display: block;
        }
      }

      @media (max-width: 375px) {
        img {
          max-width: 120px;
        }
      }
    }

    &__line {
      padding-bottom: 0 !important;
      @include paddingContent();

      @media (max-width: 992px) {
        display: none;
      }
    }

    &__content {
      @include paddingContent();

      @media (min-width: 769px) and (max-width: 992px) {
        flex-grow: 1;
        &-p {
          display: grid;
          width: 100%;
          grid-template-columns: 1fr 1fr;
        }
      }

      h4 {
        font-family: "Roboto";
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 27px;
        color: $gray;

        @include fluidFontSize(20, 24, 320, 1920);

        @media (max-width: 375px) {
          font-size: 18px;
        }
      }

      p {
        margin-top: 24px;

        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 22px;
        line-height: 115%;
        color: $gray;

        @include fluidFontSize(18, 22, 992, 1920);

        @media (max-width: 768px) {
          margin-top: 10px;
          font-size: 16px;
        }

        @media (max-width: 375px) {
          font-size: 14px;
        }

        &:last-child {
          margin-top: 14px;

          @media (max-width: 768px) {
            margin-top: 5px;
          }

          span {
            display: block;
          }
        }

        span {
          font-weight: 700;
          white-space: nowrap;
        }
      }
    }
  }
}
