@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
#area article {
  background: #ffffff;
  border: 3px solid #ececec;
  box-shadow: 15px 15px 40px rgba(0, 0, 0, 0.1);
  border-radius: 20px;
}

#area {
  padding-bottom: 100px;
}
@media (max-width: 992px) {
  #area {
    padding-bottom: 80px;
  }
}
@media (max-width: 768px) {
  #area {
    padding-bottom: 60px;
  }
}

#area .area-wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 30px;
}
@media (max-width: 1280px) {
  #area .area-wrapper {
    gap: 20px;
  }
}
@media (max-width: 1024px) {
  #area .area-wrapper {
    gap: 15px;
  }
}
@media (max-width: 992px) {
  #area .area-wrapper {
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: 1fr;
  }
}
#area article {
  overflow: hidden;
}
#area .aria-item {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
@media (max-width: 1280px) {
  #area .aria-item {
    gap: 25px;
  }
}
@media (max-width: 1024px) {
  #area .aria-item {
    gap: 20px;
  }
}
@media (max-width: 992px) {
  #area .aria-item {
    flex-direction: row;
    align-items: center;
    gap: 50px;
  }
}
@media (max-width: 425px) {
  #area .aria-item {
    gap: 20px;
  }
}
#area .aria-item__img {
  width: 100%;
}
#area .aria-item__img img {
  width: 100%;
}
@media (max-width: 992px) {
  #area .aria-item__img {
    width: auto;
  }
  #area .aria-item__img img {
    width: auto;
    height: 100%;
    display: block;
  }
}
@media (max-width: 375px) {
  #area .aria-item__img img {
    max-width: 120px;
  }
}
#area .aria-item__line {
  padding-bottom: 0 !important;
}
@media (min-width: 993px) {
  #area .aria-item__line {
    padding: 0 40px 50px;
  }
}
@media (min-width: 993px) and (max-width: 1280px) {
  #area .aria-item__line {
    padding: 0 30px 40px;
  }
}
@media (min-width: 993px) and (max-width: 1080px) {
  #area .aria-item__line {
    padding: 0 20px 30px;
  }
}
@media (max-width: 992px) {
  #area .aria-item__line {
    display: none;
  }
}
@media (min-width: 993px) {
  #area .aria-item__content {
    padding: 0 40px 50px;
  }
}
@media (min-width: 993px) and (max-width: 1280px) {
  #area .aria-item__content {
    padding: 0 30px 40px;
  }
}
@media (min-width: 993px) and (max-width: 1080px) {
  #area .aria-item__content {
    padding: 0 20px 30px;
  }
}
@media (min-width: 769px) and (max-width: 992px) {
  #area .aria-item__content {
    flex-grow: 1;
  }
  #area .aria-item__content-p {
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr;
  }
}
#area .aria-item__content h4 {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 27px;
  color: #2b2b2b;
  font-size: calc(0.0025 * 100vw + 19.2px);
}
@media (min-width: 1921px) {
  #area .aria-item__content h4 {
    font-size: 24px;
  }
}
@media (max-width: 320px) {
  #area .aria-item__content h4 {
    font-size: 20px;
  }
}
@media (max-width: 375px) {
  #area .aria-item__content h4 {
    font-size: 18px;
  }
}
#area .aria-item__content p {
  margin-top: 24px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 115%;
  color: #2b2b2b;
  font-size: calc(0.0043103448 * 100vw + 13.724137931px);
}
@media (min-width: 1921px) {
  #area .aria-item__content p {
    font-size: 22px;
  }
}
@media (max-width: 992px) {
  #area .aria-item__content p {
    font-size: 18px;
  }
}
@media (max-width: 768px) {
  #area .aria-item__content p {
    margin-top: 10px;
    font-size: 16px;
  }
}
@media (max-width: 375px) {
  #area .aria-item__content p {
    font-size: 14px;
  }
}
#area .aria-item__content p:last-child {
  margin-top: 14px;
}
@media (max-width: 768px) {
  #area .aria-item__content p:last-child {
    margin-top: 5px;
  }
}
#area .aria-item__content p:last-child span {
  display: block;
}
#area .aria-item__content p span {
  font-weight: 700;
  white-space: nowrap;
}